import React from "react";

import classes from "./MiniCard.module.css";
import DateFnsUtils from "@date-io/date-fns";
import {
  MuiPickersUtilsProvider,
  KeyboardDateTimePicker
} from "@material-ui/pickers";

const miniCard = props => {
  return (
    <div className={classes.miniCard} onClick={props.cardSelect}>
      <p className={classes.cardTitle}>{props.details.taskTitle}</p>
      <div className={classes.cardDescription}>
        {props.details.taskDescription}
      </div>
      <div className={classes.cardDueDateTime}>
        <MuiPickersUtilsProvider utils={DateFnsUtils}>
          <KeyboardDateTimePicker
            margin="normal"
            id="date-picker-dialog"
            label="Due on"
            format="MM/dd/yyyy hh:mm a"
            input={props.details.dueDateTime}
            readOnly={true}
            KeyboardButtonProps={{
              "aria-label": "change date"
            }}
          />
        </MuiPickersUtilsProvider>
      </div>
    </div>
  );
};

export default miniCard;
