import React, { Component } from "react";
import classes from "./TaskBoard.module.css";

import MiniCard from "../../components/MiniCard/MiniCard";
import Modal from "../../components/UI/Modal/Modal";
import TaskForm from "../../components/TaskForm/TaskForm";

class TaskBoard extends Component {
  state = {
    tasks: [
      {
        id: 1,
        taskTitle: "First Task",
        taskDescription:
          "In the example above, the ondragstart attribute calls a function, drag(event), that specifies what data to be dragged.In the example above, the ondragstart attribute calls a function, drag(event), that specifies what data to be dragged.",
        createdDateTime: "",
        dueDateTime: "",
        status: ""
      },
      {
        id: 2,
        taskTitle: "Second Task",
        taskDescription: "lorem lipsum",
        createdDateTime: "",
        dueDateTime: "",
        status: "Done"
      },
      {
        id: 3,
        taskTitle: "Second Task",
        taskDescription: "lorem lipsum",
        createdDateTime: "",
        dueDateTime: "",
        status: "Done"
      },
      {
        id: 4,
        taskTitle: "Second Task",
        taskDescription: "lorem lipsum",
        createdDateTime: "",
        dueDateTime: "",
        status: "Done"
      }
    ],
    selectedTask: "1",
    showTask: false,
    selectedDate: new Date("2014-08-18T21:11:54")
  };

  inputChangeHandler = (event, taskId, inputIdentifier) => {
    const stateCopy = { ...this.state };

    let tasks = stateCopy.tasks;
    let selectedTask = tasks.find(task => task.id === taskId);
    if (inputIdentifier === "dueDateTime") {
      selectedTask[inputIdentifier] = event;
    } else {
      selectedTask[event.target.id] = event.target.value;
    }

    this.setState({ tasks: tasks });
  };

  openTask = selectedTask => {
    this.setState({ selectedTask: selectedTask, showTask: true });
  };

  modalClose = () => {
    this.setState({ showTask: false });
  };

  render() {
    let miniCardList = this.state.tasks.map(task => {
      return (
        <MiniCard
          key={task.id}
          details={task}
          cardSelect={() => this.openTask(task.id)}
        ></MiniCard>
      );
    });

    let taskForm = null;

    if (this.state.showTask) {
      taskForm = (
        <TaskForm
          task={this.state.tasks.find(task => {
            return task.id === this.state.selectedTask;
          })}
          inputChangeHandler={this.inputChangeHandler}
          modalClosed={this.modalClose}
        ></TaskForm>
      );
    }

    return (
      <div>
        <Modal show={this.state.showTask} modalClosed={this.modalClose}>
          {taskForm}
        </Modal>
        <div className={classes.taskBoard}>{miniCardList}</div>
      </div>
    );
  }
}

export default TaskBoard;
