import React from "react";

import DateFnsUtils from "@date-io/date-fns";
import {
  MuiPickersUtilsProvider,
  KeyboardDateTimePicker
} from "@material-ui/pickers";
import classes from "./TaskForm.module.css";

const taskForm = props => {
  return (
    <div className={classes.taskForm}>
      <button className={classes.modalCloseBtn} onClick={props.modalClosed}>
        x
      </button>
      <input
        id="taskTitle"
        className={classes.taskTitle}
        placeholder="Title"
        onChange={event => props.inputChangeHandler(event, props.task.id)}
        value={props.task.taskTitle}
      >
        {props.title}
      </input>
      <textarea
        id="taskDescription"
        className={classes.taskDescription}
        placeholder="Description"
        onChange={event => props.inputChangeHandler(event, props.task.id)}
        value={props.task.taskDescription}
      >
        {props.description}
      </textarea>
      <div className={classes.customDatePicker}>
        <MuiPickersUtilsProvider utils={DateFnsUtils}>
          <KeyboardDateTimePicker
            margin="normal"
            id="date-picker-dialog"
            label="Due on"
            format="MM/dd/yyyy hh:mm a"
            input={props.task.dueDateTime}
            onChange={event =>
              props.inputChangeHandler(event, props.task.id, "dueDateTime")
            }
            KeyboardButtonProps={{
              "aria-label": "change date"
            }}
          />
        </MuiPickersUtilsProvider>
      </div>
      <button className={classes.taskCompleteButton}>Mark as Completed</button>
    </div>
  );
};

export default taskForm;
